export const ExportXLSX = (csvData) => {
  const filename = `Order backlog ${moment().format("MMMM Do YYYY-h:mm:ss-a")}.xlsx`;
  const fileType = "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
  const ws = XLSX.utils.json_to_sheet(csvData);
  const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
  const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
  const data = new Blob([excelBuffer], { type: fileType });
  FileSaver.saveAs(data, filename);
};

function ExportCSV(data) {
  if (!data.length) {
    return [
      {
        OrderId: "",
        Customer: "",
        Description: "",
        Date: "",
        Amount: "",
        Status: "",
      },
    ];
  }
  const csvData = data.map((element) => ({
    OrderId: get(element, "document_number", ""),
    Customer: get(element, "customer.name", ""),
    Description: get(element, "order_lines[0].description", ""),
    Date: moment(get(element, "date", "")).format("MM/DD/YYYY"),
    Amount: convertDecimalNo(get(element, "total_amount_open", 0)),
    Status: get(element, "status", ""),
  }));

  return csvData;
}

