import React, { useState } from "react";
import { connect } from "react-redux";
import { Route, Switch, withRouter } from "react-router-dom";
import "assets/css/payables.css";
import BacklogList from "./BacklogList";
import BacklogDetailsPage from "./BacklogDetailsPage";

function Backlogs(props) {
  document.title = "Order Backlog - Payference";
  const { history, match } = props;
  const [selectedpayment, setselectedpayment] = useState(null);

  function changeSelection(data) {
    setselectedpayment(data);
    history.push(`backlog/${data.id}`);
  }

  return (
    <div>
      <div className="row px-15" id="dashboardBody">
        <div className="col-md-12">
          <div className="card">
            <Switch>
              <Route path={`${match.path}/:paymentid`} component={() => <BacklogDetailsPage {...props} selectedpayment={selectedpayment} />} />
              <Route path={`${match.path}`} component={() => <BacklogList {...props} changeSelection={changeSelection} />} />
            </Switch>
          </div>
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = state => ({ login: state.payables });

const mapDispatchToProps = {};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Backlogs));
